body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.transition-async {
  background-color: yellow;
}

.transition-async-enter {
  opacity: 0.01;
  -webkit-transform: scale(0.9) translateY(50%);
          transform: scale(0.9) translateY(50%);
  background-color: #61dafb;
}
.transition-async-enter-active {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0%);
          transform: scale(1) translateY(0%);
  transition: all 300ms ease-out;
}
.transition-async-exit {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0%);
          transform: scale(1) translateY(0%);
}
.transition-async-exit-active {
  opacity: 0.01;
  -webkit-transform: scale(0.9) translateY(50%);
          transform: scale(0.9) translateY(50%);
  transition: all 300ms ease-out;
}


.transition-import {
  background-color: red;
}

.transition-import-enter {
  opacity: 0.01;
  -webkit-transform: scale(0.1) translateY(50%);
          transform: scale(0.1) translateY(50%);
  background-color: #61dafb;
}
.transition-import-enter-active {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0%);
          transform: scale(1) translateY(0%);
  transition: all 1000ms ease-out;
}
.transition-import-exit {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0%);
          transform: scale(1) translateY(0%);
}
.transition-import-exit-active {
  opacity: 0.01;
  -webkit-transform: scale(0.1) translateY(50%);
          transform: scale(0.1) translateY(50%);
  transition: all 1000ms ease-out;
}
